<template>
	<view class="page_goods" id="goods_details">
		<!-- 商品详情 -->
		<div_goods style="background-color: #fff;margin-bottom: 0.5rem;" :obj="obj"></div_goods>

		<!-- 评论区标题 -->
		<bar_title :title="bar_title" :url="bar_url"></bar_title>

		<!-- 评论区列表 -->
		<list_comment style="background-color: #fff;" :list="list_comment"></list_comment>

		<!-- 购买栏 -->
		<view class="buy_wrap">
			<bar_buy :obj="obj"></bar_buy>
		</view>
	</view>
</template>

<script>
	import div_goods from "../../components/diy/div_goods.vue";
	import list_comment from "../../components/diy/list_comment.vue";
	import bar_title from "../../components/diy/bar_title.vue";
	import bar_buy from "../../components/diy/bar_buy.vue";

	import mixin from "../../mixins/page.js";
	export default {
		mixins: [mixin],
		components: {
			div_goods,
			list_comment,
			bar_title,
			bar_buy
		},
		data() {
			return {
				url_get_obj: "~/api/goods/get_obj?",
				field: "goods_id",
				query: {
					goods_id: 0
				},
				href: 'https://uniapp.dcloud.io/component/README?id=uniui',
				// 商品详情初始化
				obj: {
				},
				// 评论列表初始化
				list_comment: [
				],
				// 评论bar标题
				bar_title: "评论区",
				// 评论bar的链接
				bar_url: "",
			}
		},
		methods: {
			get_obj_after(json) {
				this.get_comment(json.result.obj)
			},
			// 获取评论
			get_comment(obj) {
				this.$get("~/api/comment/get_list?", {
					source_table:"goods",
					source_field:"goods_id",
					source_id:obj.goods_id
				}, (json) => {
					if (json.result) {
						var list_comment = json.result.list
						this.list_comment = list_comment
					}
				})
			}
		},
	}
</script>

<style>
	.page_goods {
		padding-bottom: 3rem;
	}

	.link {
		text-align: center;
		padding: 0.5rem 0;
		border: 1px solid #DBDBDB;
		border-radius: 0.5rem;
	}

	.buy_wrap {
		position: fixed;
		bottom: 0;
		left: 0;
		width: 100%;
	}
</style>
